/* font converted using font-converter.net. thank you! */
@font-face {
  font-family: "Apercu-Mono";
  src: url("./fonts/Apercu-Mono.woff") format("woff"), url("./fonts/Apercu-Mono.woff2") format("woff2");
  /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'Apercu-Mono';
}

body {
  background-color: white;
}

.heading {
  margin: 10vw 5vw;
  color: #FF9E1B;
  display: flex;
  justify-content: center;
}

.heading__logo {
  position: absolute;
  z-index: 10;
  width: 50vw;
  height: auto;
  transform: translateY(-20vw);
}

@media (min-width: 768px) {
  .heading__logo {
    transform: translateY(-20vw);
  }
}

@media (min-width: 768px) {
  .heading {
    margin: 10vw;
  }
}

.heading__wrap {
  position: relative;
}

.heading__canvas {
  width: 90vw;
  height: auto;
  margin: 0 auto;
  display: block;
}

.text {
  margin: 10vw;
}

.text__title {
  text-align: center;
  font-size: 24px;
}

@media (min-width: 768px) {
  .text__title {
    font-size: 80px;
    margin-top: 24px;
  }
}

.text__body, .text__close {
  text-align: center;
  font-size: 18px;
}

@media (min-width: 768px) {
  .text__body, .text__close {
    font-size: 32px;
    margin-top: 12px;
  }
}

.text__close {
  margin: 56px 0;
}

.image {
  opacity: 0;
  transform: translateY(80px);
  transition: opacity 400ms ease, transform 800ms ease;
}

.image.active {
  opacity: 1;
  transform: translateY(0);
}

.image--left {
  width: 65vw;
}

@media (min-width: 768px) {
  .image--left {
    margin-right: 2.5vw;
    width: 40vw;
  }
}

.image--right {
  width: 65vw;
  align-self: flex-end;
  margin-top: 24px;
}

@media (min-width: 768px) {
  .image--right {
    margin-top: 0;
    margin-left: 2.5vw;
    width: 40vw;
  }
}

.image--center {
  width: 90vw;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .image--center {
    width: 80vw;
  }
}

.image__wrap {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .image__wrap {
    flex-direction: row;
    margin: 10vw;
  }
}

.link {
  color: #000;
  transition: color 250ms ease, transform 250ms ease;
  transform: skewX(0);
  display: inline-block;
}

.link:hover {
  color: #4a6741;
  transform: skewX(-20deg);
  cursor: pointer;
}

.video {
  width: 100%;
}

.video__wrap {
  width: 90vw;
  margin: 0 auto;
}

#video__canvas {
  height: 0;
}
